body {
  font-family: 'Rubik', sans-serif;
  background-color: #000000;
  color: #ffffff;
  min-width: 320px;
  margin: 0;
  padding: 0; }

.section-inner {
  margin: 0 auto;
  padding: 0 10px; }
  @media (min-width: 568px) {
    .section-inner {
      padding: 0 3%; } }
  @media (min-width: 1200px) {
    .section-inner {
      max-width: 1170px; } }

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none;
  color: #ffffff; }

.top-bar {
  background-color: #212121;
  color: #ffffff;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  opacity: .95; }
  .top-bar__nojs {
    position: static; }
    .top-bar__nojs .top-bar__pic {
      display: none; }
  .top-bar .section-inner {
    position: relative; }
  .top-bar__pic {
    display: flex;
    align-items: center;
    margin: 10px; }
  .top-bar__heart {
    animation: heartbeat 2s infinite;
    transition: 0.3s; }
  .top-bar__toggle {
    position: absolute;
    top: -10px;
    right: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
    width: 60px;
    height: 54px;
    border: none;
    padding: 0;
    margin: 0;
    background-color: inherit;
    user-select: none;
    outline: none;
    cursor: pointer; }
  .top-bar__btn {
    position: relative;
    display: inline-block;
    width: 26px;
    height: 2px;
    vertical-align: bottom;
    background: #ffffff;
    transition: all 0.7s; }
  .top-bar__btn::after, .top-bar__btn::before {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #ffffff;
    transform-origin: 50% 50%;
    transition: all 0.5s; }
  .top-bar__btn::after {
    top: -6px; }
  .top-bar__btn::before {
    top: 6px; }
  .top-bar__opened .top-bar__btn::after,
  .top-bar__opened .top-bar__btn::before {
    top: 0;
    transition: all 0.5s;
    width: 20px; }
  .top-bar__opened .top-bar__btn::before {
    transform: rotate3d(0, 0, 1, 45deg); }
  .top-bar__opened .top-bar__btn::after {
    transform: rotate3d(0, 0, 1, -45deg); }
  .top-bar__opened .top-bar__btn {
    background: 0 0;
    transition: background 0.01s 0.01s; }
  .top-bar__nojs .top-bar__list,
  .top-bar__opened .top-bar__list {
    display: block; }
  .top-bar__nojs .top-bar__toggle {
    display: none; }
  .top-bar__list {
    display: none;
    list-style: none;
    padding: 0 60px 20px;
    margin: 0;
    font-size: 16px;
    min-height: 50px;
    text-align: center; }
  .top-bar__item {
    font-weight: 500;
    transition: 0.3s; }
  .top-bar__link {
    display: block;
    padding: 15px 20px; }
    .top-bar__link:hover {
      color: #ec008c; }

@media (min-width: 568px) {
  .top-bar .section-inner {
    display: flex;
    justify-content: flex-end; }
  .top-bar__pic {
    margin: 0 10px;
    order: 1; }
  .top-bar__nav {
    display: flex; }
  .top-bar__toggle {
    display: none; }
  .top-bar__list,
  .top-bar__nojs .top-bar__list,
  .top-bar__opened .top-bar__list {
    display: flex;
    align-self: center;
    padding: 0; } }

@keyframes heartbeat {
  0% {
    transform: scale(1); }
  20% {
    transform: scale(1.15) translateX(2%) translateY(2%); }
  40% {
    transform: scale(1.2) translateX(4%) translateY(5%); } }

.title {
  background-color: #000000;
  min-height: 100vh;
  display: grid;
  align-items: center; }
  .title__container {
    padding: 60px 0;
    font-size: 16px;
    text-align: center; }
  .title__text {
    font-weight: 500;
    font-size: 23px;
    margin: 40px auto;
    width: 90%; }
  .title__avatar {
    position: relative;
    height: 125px;
    width: 125px;
    margin: 0 auto; }
  .title__img {
    position: absolute;
    overflow: hidden; }
    .title__img img {
      display: block;
      width: 125px;
      height: 125px;
      object-fit: cover;
      border-radius: 50%; }
  .title .slider {
    position: absolute;
    z-index: 2;
    cursor: ew-resize;
    width: 20px;
    height: 20px;
    background-color: #7c7c7d;
    opacity: 0.7;
    border-radius: 50%; }
    .title .slider:hover {
      background-color: #ec008c;
      opacity: 1; }
  @media (min-width: 568px) {
    .title__text {
      width: 80%; } }
  @media (min-width: 768px) {
    .title__text {
      width: 60%;
      font-size: 30px;
      line-height: 50px; } }

.portfolio {
  background-color: #000000;
  padding: 25% 0 3%; }
  .portfolio__title {
    width: 270px;
    margin-left: 20px; }
  .portfolio__card {
    margin: 50px 20px;
    background: linear-gradient(to top right, #65316b, #009eb5);
    padding: 5px;
    transition: 1s;
    filter: grayscale(100%); }
    .portfolio__card:hover {
      filter: none; }
  .portfolio__pic {
    width: 100%;
    height: auto;
    display: block; }
  .portfolio__desription {
    min-height: 60px;
    padding: 2% 5% 7%; }
    .portfolio__desription h3 {
      font-weight: 400;
      text-align: left;
      font-size: 16px;
      line-height: 20px; }
    .portfolio__desription p {
      font-weight: 300;
      text-align: left;
      font-size: 15px;
      opacity: .7; }
  @media (min-width: 568px) {
    .portfolio {
      padding: 7% 0 3%; }
      .portfolio__cards {
        display: grid;
        grid-template-columns: 1fr 1fr; }
      .portfolio__card {
        margin: 20px; } }
  @media (min-width: 768px) {
    .portfolio__title {
      margin-bottom: 20px; } }
  @media (min-width: 1200px) {
    .portfolio__cards {
      grid-template-columns: 1fr 1fr 1fr; } }

.skills {
  background-color: #000000;
  padding: 30% 0 3%;
  font-weight: 500;
  font-size: 22px;
  line-height: 44px; }
  .skills__title {
    margin: 0 0 20px 20px; }
  .skills__list {
    padding: 10px;
    margin: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap; }
    .skills__list li {
      margin: 0 10px; }
  .skills span {
    margin: 10px;
    padding: 5px;
    background-color: #000000;
    border-radius: 7px; }
  @media (min-width: 568px) {
    .skills {
      font-size: 22px;
      line-height: 44px; } }
  @media (min-width: 768px) {
    .skills {
      padding: 7% 0 3%;
      font-size: 25px; } }
  @media (min-width: 1200px) {
    .skills {
      font-size: 27px;
      line-height: 50px; } }

.certificates {
  background-color: #000000;
  padding: 25% 0 7%; }
  .certificates__title {
    margin: 0 0 20px 20px; }
  .certificates__wrap {
    height: 90vh;
    margin: 0;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 32px;
    align-items: center;
    position: relative; }
  .certificates__controls {
    display: flex;
    justify-content: space-between;
    grid-column: 1;
    grid-row: 1;
    width: 100%;
    z-index: 3;
    cursor: pointer; }
    .certificates__controls:hover ~ .certificates__item {
      filter: none; }
  .certificates__item {
    display: none;
    width: 80%;
    margin: 0 auto;
    z-index: 1;
    transition: all .6s;
    grid-column: 1;
    grid-row: 1;
    filter: grayscale(100%); }
    .certificates__item:hover {
      filter: none; }
    .certificates__item--showing {
      display: grid;
      align-items: center; }
  .certificates .next:active,
  .certificates .prev:active {
    transform: scale(0.9); }
  .certificates__link {
    text-align: center; }
  .certificates__pic {
    max-width: 100%;
    max-height: 80vh;
    margin: 0; }
  .certificates__wrap--nojs {
    grid-template-rows: repeat(5, 1fr);
    height: auto; }
    .certificates__wrap--nojs .certificates__controls {
      display: none; }
    .certificates__wrap--nojs .certificates__item {
      display: grid;
      align-items: center;
      margin-bottom: 30px;
      grid-row: 1 span;
      opacity: 1; }
    .certificates__wrap--nojs .slider__indicators {
      display: none; }
  .certificates .slider__indicators {
    list-style: none;
    grid-row: 2;
    grid-column: 1;
    display: flex;
    padding: 0;
    justify-content: center;
    width: 100%; }
  .certificates .slider__indicators li {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: 0 10px;
    background-color: #000000;
    opacity: 0.5;
    box-shadow: 0 0 3px rgba(255, 255, 255, 0.5);
    transition: all 1s;
    cursor: pointer; }
  .certificates .slider__indicators li:hover {
    background-color: #ffffff; }
  .certificates .slider__indicators .active {
    background-color: #ffffff;
    opacity: 1;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5); }
  @media (min-width: 568px) {
    .certificates__wrap--nojs {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto; }
      .certificates__wrap--nojs .certificates__item {
        grid-column: 1 span; } }
  @media (min-width: 768px) {
    .certificates {
      padding: 7% 0 10%; }
      .certificates .certificates__title {
        margin: 0 0 50px 20px; } }
  @media (min-width: 1200px) {
    .certificates__wrap--nojs {
      grid-template-columns: 1fr 1fr 1fr; } }

@keyframes display {
  0% {
    opacity: 0; }
  10% {
    opacity: 0; }
  50% {
    opacity: 0.1; }
  60% {
    opacity: 0.2; }
  70% {
    opacity: 0.4; }
  80% {
    opacity: 0.6; }
  90% {
    opacity: 0.8; }
  100% {
    opacity: 1; } }

@keyframes fade {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.5; }
  100% {
    opacity: 0; } }

.footer {
  background-color: #212121;
  color: #ffffff;
  width: 100%; }
  .footer .section-inner {
    text-align: center; }
  .footer__social {
    display: flex;
    justify-content: center;
    padding: 30px 0 0; }
  .footer__link {
    padding: 5px;
    transition: 0.3s;
    background: #ffffff;
    border-radius: 50%;
    margin: 10px;
    width: 40px;
    height: 40px; }
    .footer__link:hover {
      transform: scale(1.2);
      background: linear-gradient(to bottom left, #f05a28, #ec008c); }
    .footer__link:active {
      transform: scale(0.9); }
  .footer__copyright {
    padding: 0 0 20px;
    font-weight: 300;
    font-size: 14px;
    opacity: .8; }
